import { getNowDateTime, NowTimeStamp } from "@/utils/index";
import { GroupType, GroupFunc, DiscussFunc, ScreenFunc, PracticeFunc, LimitType } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";

let that = null;
let gatewayConn = null;
let startServer = false; //用来识别是否开启服务
const gatewaySignalR = {
  router: null,
  reConnect: false,
  heartTimeStamp: 0,
  init(obj) {
    that = obj;
  },
  openGatewaySocket() {
    if (!that.$store.getters.gatewayIP) return;
    if (!that.$route.meta || !that.$route.meta.limits || that.$route.meta.limits.indexOf(LimitType.inCourse) === -1) {
      return;
    }
    gatewaySignalR.reConnect = true;
    gatewayConn = $.hubConnection("http://" + that.$store.getters.gatewayIP + ":" + that.$config.gatewaySignalRPort);
    window.GatewayHub = gatewayConn.createHubProxy("TerminalHub");
    gatewayConn.qs = { StudentID: that.$store.getters.studentID };
    gatewaySignalR.signalRFunction();

    gatewayConn
      .start()
      .done(data => {
        console.log(getNowDateTime(), "gatewaySignal服务启动成功");
        gatewaySignalR.signalRGroup();
      })
      .fail(err => {
        console.log(getNowDateTime(), "gatewaySignal服务连接失败", err);
        // setTimeout(() => {
        //   gatewayConn.start();
        // }, 1000 * 3);
      });

    gatewayConn.disconnected(() => {
      console.log(getNowDateTime(), "gatewaySignal连接已断开", startServer);
    });

    // stateChanged：连接状态更改时引发。 提供的旧状态和新的状态 （连接、 已连接、 正在重新连接或已断开连接）。
    gatewayConn.stateChanged(data => {
      console.log("data:", data);
      console.log("通信状态：", data.newState, this.reConnect);
      if (data.newState !== 1 && startServer) {
        gatewaySignalR.startGatewaySignalR();
      } else {
        gatewaySignalR.reConnect = false;
        if (data.newState === 1) {
          gatewaySignalR.getScreenInfo();
          gatewaySignalR.validateCourse();
        }
        // 需先初始化心跳记录时间点
        gatewaySignalR.heartTimeStamp = 0;
        gatewaySignalR.startHeart();
      }
    });
  },
  /**
   * signalR组管理
   */
  signalRGroup() {
    window.GatewayHub.invoke("QuitGroup", "All")
      .then(res => {
        console.log("退出ALL组成功");
      })
      .catch(err => {
        console.log("err：", err);
      });

    setTimeout(() => {
      window.GatewayHub.invoke("JoinGroup", "All")
        .then(res => {
          console.log("加入ALL组成功");
        })
        .catch(err => {
          console.log("err：", err);
        });
    }, 500);

    window.GatewayHub.invoke("QuitGroup", that.$store.getters.studentID)
      .then(res => {
        console.log("退出个单人组:", that.$store.getters.studentID);
      })
      .catch(err => {
        console.log("err：", err);
      });
    setTimeout(() => {
      window.GatewayHub.invoke("JoinGroup", that.$store.getters.studentID)
        .then(res => {
          console.log("加入单人组：", that.$store.getters.studentID);
        })
        .catch(err => {
          console.log("err：", err);
        });
    }, 500);

    // 自动加入分组
    that.$store.dispatch("GetGroupByUser").then(res => {
      if (res.Result) {
      }
    });
  },
  /**
   * signalR响应事件
   */
  signalRFunction() {
    // 课程
    window.GatewayHub.on("ReciveStartClass", courseId => {
      // console.log("ReciveStartClass:", courseId);
      ReciveStartClass({ state: 1 });
    });
    window.GatewayHub.on("ReciveOverClass", value => {
      ReciveStartClass({ state: 0 });
    });

    // 接收消息(截屏提问) --接收题目
    window.GatewayHub.on("ReciveSSSubject", subjectId => {
      reciveScreenSubject({ id: subjectId, type: ScreenFunc.init });
    });
    // --接收答案
    window.GatewayHub.on("ReciveSSAnswer", subjectId => {
      reciveScreenSubject({ id: subjectId, type: ScreenFunc.acceptAnswer });
    });
    // --提交调研
    window.GatewayHub.on("ReciveSSSurvey", subjectId => {
      reciveScreenSubject({ id: subjectId, type: ScreenFunc.survey });
    });
    // 截屏提问--开始抢答
    window.GatewayHub.on("ReciveSSStartResponder", subjectId => {
      reciveScreenSubject({ id: subjectId, type: ScreenFunc.responder });
    });

    // 接收消息(随堂练习)
    window.GatewayHub.on("RecivePSStart", data => {
      console.log("RecivePSStart:", data);
      reciveClassPracice({
        id: data["CIPID"],
        bankId: data["PLID"],
        no: data["PSSerial"],
        total: data["SubjectTotal"],
        type: PracticeFunc.start
      });
    });
    window.GatewayHub.on("RecivePSEnd", data => {
      console.log("RecivePSEnd:", data);
      reciveClassPracice({
        id: "",
        type: PracticeFunc.end
      });
    });
    window.GatewayHub.on("RecivePSubject", data => {
      console.log("RecivePSubject:", data);
      reciveClassPracice({
        id: data["CIPID"],
        bankId: data["PLID"],
        no: data["PSSerial"],
        // total: data["SubjectTotal"],
        type: PracticeFunc.init
      });
    });
    window.GatewayHub.on("RecivePSAnswer", practiceId => {
      console.log("RecivePSAnswer:", practiceId);
      reciveClassPracice({ id: practiceId, type: PracticeFunc.acceptAnswer });
    });
    window.GatewayHub.on("RecivePSSurvey", practiceId => {
      console.log("RecivePSSurvey:", practiceId);
      reciveClassPracice({ id: practiceId, type: PracticeFunc.survey });
    });

    // 小组分组
    window.GatewayHub.on("ReciveGrouping", groupType => {
      reciveGrouping({
        groupType: groupType,
        type: GroupFunc.init,
        time: NowTimeStamp()
      }); //开始分组
    });
    window.GatewayHub.on("ReciveUpdateGroupName", groupName => {
      console.log(groupName);
      reciveGrouping({ type: GroupFunc.reName, time: NowTimeStamp() }); //更新组名
    });
    window.GatewayHub.on("ReciveJoinGroup", groupID => {
      reciveGrouping({ type: GroupFunc.newJoin, time: NowTimeStamp() }); // 接收加入小组
    });
    window.GatewayHub.on("ReciveQuitGroup", groupID => {
      reciveGrouping({ type: GroupFunc.quit, time: NowTimeStamp() }); // 接收退出小组
    });
    window.GatewayHub.on("ReciveSetGroupLeader", studentId => {
      reciveGrouping({ type: GroupFunc.setLead, time: NowTimeStamp() }); // 更新组长
    });

    //小组讨论
    window.GatewayHub.on("ReciveGroupSubject", discussionId => {
      reciveGroupDiscussion({ type: DiscussFunc.init, value: discussionId });
    });
    window.GatewayHub.on("ReciveGroupDiscuss", discussion => {
      reciveGroupDiscussion({ type: DiscussFunc.newMsg, value: discussion });
    });
    window.GatewayHub.on("ReciveGroupGood", value => {
      reciveGroupDiscussion({ type: DiscussFunc.good, value: value });
    });
    window.GatewayHub.on("ReciveGroupScore", value => {
      reciveGroupDiscussion({ type: DiscussFunc.score, value: value });
    });

    // 板书分享
    window.GatewayHub.on("ReciveScreenShare", value => {
      reciveBoardNote({ id: value });
    });

    // 广播
    window.GatewayHub.on("ReciveStartBroadcast", value => {
      console.log("start:", value);
      // reciveBoardCast({ ip: value.BType, ids: value.ID || "", state: 1 });
      reciveBoardCast({ ip: value.IP || value.BType, ids: value.ID || "", state: 1 });
    });
    window.GatewayHub.on("ReciveStopBroadcast", value => {
      console.log("stop:", value);
      reciveBoardCast({ ip: value, ids: "", state: 0 });
    });

    // 投屏
    window.GatewayHub.on("ReciveStartThrowScreen", value => {
      console.log("startThrow:", value);
      reciveThrowScreen({ ip: value, state: 1 });
    });
    window.GatewayHub.on("ReciveStopThrowScreen", value => {
      console.log("stopThrow:", value);
      reciveThrowScreen({ ip: value, state: 0 });
    });

    // 被投屏
    window.GatewayHub.on("ReciveShowThrowScreen", value => {
      console.log("startBeThrow:", value);
      reciveBeThrowScreen({ ip: value, state: 1 });
    });
    window.GatewayHub.on("ReciveStopShowThrowScreen", value => {
      console.log("stopBeThrow:", value);
      reciveBeThrowScreen({ ip: value, state: 0 });
    });

    // 投屏状态
    window.GatewayHub.on("ReciveIsAllowThrowScreen", value => {
      reciveThrowScreenState({ state: value });
    });
  },

  /**
   * 定时启动signalR服务，成功清除定时器
   */
  startGatewaySignalR() {
    if (!gatewaySignalR.reConnect) {
      startServer = true;
      console.log(getNowDateTime(), "请求启动gatewaySignalR");
      gatewaySignalR.reConnectServer();
    }
  },
  /**
   * 递归启动signalR服务
   */
  reConnectServer() {
    // 不在上课期间，不用启动
    if (!that.$route.meta || !that.$route.meta.limits || that.$route.meta.limits.indexOf(LimitType.inCourse) === -1) {
      return;
    }
    if (!gatewayConn || gatewayConn.state !== 1) {
      // console.log(this.getNowDateTime(), "检测到signalR服务未连接");
      gatewaySignalR.openGatewaySocket();
      setTimeout(() => {
        gatewaySignalR.reConnectServer();
      }, 2000);
    }
  },
  /**
   * 停止signalR服务
   */
  stopServer() {
    startServer = false;
    gatewaySignalR.reConnect = false;
    console.log(getNowDateTime(), "关闭服务");
    if (gatewayConn) {
      gatewayConn.stop(false, true);
    }
    if (that && that.timerSignalR) {
      window.clearInterval(that.timerSignalR);
    }
  },
  startRegister() {
    let IP = that.$store.getters.localIP;
    sendToAPP(FuncType.initLocalIP, "").then(res => {
      if (res) that.$store.commit("SET_IP", res);
    });
    let deviceType = -1;
    if (window.Android) {
      deviceType = 1;
    } else if (window.webkit) {
      deviceType = 2;
    }
    let reqData = {
      studentID: that.$store.getters.studentID,
      studentIP: that.$store.getters.localIP || IP,
      deviceType: deviceType
    };
    that.$gatewayHttp.student
      .getRegister(reqData)
      .then(res => {
        if (res.Result) {
          console.log(getNowDateTime(), "更新IP请求成功");
        }
      })
      .catch(err => { });
  },
  startHeart() {
    let timeStamp = new Date().getTime();
    let timeOut = that.$config.SignalRHeartTime || 2000;
    //防止多次调用，频繁执行
    if (timeStamp - gatewaySignalR.heartTimeStamp > timeOut) {
      gatewaySignalR.heartTimeStamp = timeStamp;
      if (gatewayConn && gatewayConn.state === 1) {
        let deviceType = -1;
        if (window.Android) {
          deviceType = 1;
        } else if (window.webkit) {
          deviceType = 2;
        }
        let message = {
          Type: 1,
          ID: that.$store.getters.studentID || "",
          IP: that.$store.getters.localIP || "",
          deviceType: deviceType
        };

        window.GatewayHub.invoke("Heart", JSON.stringify(message))
          .then(res => {
            console.log("Heart-res:", res);
            setTimeout(() => {
              gatewaySignalR.startHeart();
            }, timeOut);
          })
          .catch(err => {
            console.log("Heart-err：", err);
          });
      } else {
        // 不在上课期间，不用启动
        if (
          !that.$route.meta ||
          !that.$route.meta.limits ||
          that.$route.meta.limits.indexOf(LimitType.inCourse) === -1
        ) {
          return;
        }
        setTimeout(() => {
          gatewaySignalR.startHeart();
        }, timeOut);
      }
    }
  },
  validateCourse() {
    that.$store.dispatch('ValidateCourse').then(res => {
      if (res) {
        this.exitCourse();
      }
    }).catch(err => {
    });
  },
  exitCourse() {
    const toast = that.$toast.loading({
      duration: 0, // 持续展示 toast
      forbidClick: true,
      message: '该教室已有新课程，即将在 3 秒后退出'
    });

    let second = 3;
    const timer = setInterval(() => {
      second--;
      if (second) {
        toast.message = `该教室已有新课程，即将在  ${second} 秒后退出`;
      } else {
        clearInterval(timer);
        // 手动清除 Toast
        that.$toast.clear();

        turnPath("/home-course");
      }
    }, 1000);
  },
  getScreenInfo() {
    that.$gatewayHttp.student
      .getScreenInfo()
      .then(res => {
        if (res.Result) {
          delete res.Result;
          let info = JSON.parse(JSON.stringify(res));
          console.log(info);
          sendToAPP(FuncType.screenSetting, JSON.stringify(info))
            .then(res => {
              gatewaySignalR.startRegister();
            })
            .catch(() => {
              gatewaySignalR.startRegister();
            });
        }
      })
      .catch(err => {
        gatewaySignalR.startRegister();
      });
  },
  initGroupInfo(bl) {
    sendFreshGroup(bl);
  }
};

/**
 * 课程
 */
function ReciveStartClass(value) {
  if (value.state === 0) {
    that.$store.commit("QUIT_COURSE");
    turnPath("/home-course");
  }
  // 流程问题，暂不处理
  // if (that.$route.path.indexOf("/home-course") > -1) {
  // } else {
  //   that.$router.push("/home-course");
  // }
}

/**
 * 截屏提问
 * @param {*} value
 */
function reciveScreenSubject(value) {
  that.$store.commit("SET_SCREENQUESTION", { ...value, time: NowTimeStamp() });
  turnPath("/screenQuestion/" + value.id);
}

/**
 * 随堂练习
 * @param {*} value
 */
function reciveClassPracice(value) {
  that.$store.commit("SET_CLASSPRACTICE", { ...value, time: NowTimeStamp() });
  if (value.type === PracticeFunc.end) {
    turnPath("/quiz/1");
  } else {
    turnPath("/classPractice/" + value.id + "/" + value.bankId);
  }
}

/**
 * 接收分组
 */
function reciveGrouping(func) {
  if (func.type === GroupFunc.init && func.groupType === GroupType.Free) {
    console.log("接收分组:", func.type);
    initGroupDeal();
    return;
  }
  that.$store.dispatch("GetGroupByUser").then(res => {
    if (res.Result) {
      if (func.type === GroupFunc.quit && !that.$store.getters.groupID) {
        // 重新分组
        console.log("接收分组:", func.type);
        initGroupDeal();
      } else {
        sendFreshGroup(true);
        turnPath("/groupInfo");
      }
    }
  });
}
/**
 * 重新分组方法
 */
function initGroupDeal() {
  that.$store.commit("QUIT_GROUP");
  that.$store.commit("SET_GROUPTYPE", GroupType.Free);

  turnPath("/joinGroup");
  sendFreshGroup(false);
}

/**
 * 小组讨论
 * @param {*} value
 */
function reciveGroupDiscussion(value) {
  that.$store.commit("SET_GROUPDISCUSSION", { ...value, time: NowTimeStamp() });
  // 讨论更新的不用自动切
  if (value.type !== DiscussFunc.newMsg) {
    turnPath("/groupDiscussion");
  }
}

/**
 * 板书分享
 * @param {*} value
 */
function reciveBoardNote(value) {
  that.$store.commit("SET_BOARDNOTE", { ...value, time: NowTimeStamp() });

  turnPath("/blackboardWriting");
}

/**
 * 推送新的分组信息
 */
function sendFreshGroup(isJoin) {
  console.log("推送新的分组信息");
  let groupIp = that.$store.getters.groupIP || "";
  let groupName = "";
  let maxUser = "0";
  let connectUser = "0";
  if (that.$store.getters.group) {
    groupName = that.$store.getters.group["groupName"] || "";
    maxUser = parseInt(that.$store.getters.group["groupMax"] || "0");
    connectUser = parseInt(that.$store.getters.group["currentNum"] || "0");
  }

  let obj = {
    isJoin: isJoin,
    groupIp: groupIp,
    groupName: groupName,
    maxUser: maxUser || 0,
    connectUser: connectUser || 0
  };
  console.log("推送新值：", obj);
  sendToAPP(FuncType.groupInfo, JSON.stringify(obj));
}

/**
 * 跳转链接
 * @param toPath 即将跳转的地址
 */
function turnPath(toPath) {
  sendToAPP(FuncType.toPath, toPath);
  if (that.$route.path.indexOf(toPath) === -1) {
    that.$router.replace(toPath);
  }
}
/**
 * 推送广播信息
 * @param {*} value
 */
function reciveBoardCast(value) {
  if (value.ids && value.ids.indexOf(that.$store.getters.studentID) > -1) {
  } else {
    sendToAPP(value.state === 1 ? ((value.ip + "") === "1" || (value.ip + "") === "0" ? FuncType.broadcastStart : FuncType.broadcastStartUrl) : FuncType.broadcastStop, value.ip);
  }
}

/**
 * 推送投屏信息
 * @param {*} value
 */
function reciveThrowScreen(value) {
  sendToAPP(value.state === 1 ? FuncType.throwScreenStart : FuncType.throwScreenStop, value.ip || "-1");
}

/**
 * 推送被投屏信息
 * @param {*} value
 */
function reciveBeThrowScreen(value) {
  sendToAPP(value.state === 1 ? FuncType.beThrowScreenStart : FuncType.beThrowScreenStop, value.ip || "-1");
}
/**
 * 推送投屏状态
 * @param {*} value
 */
function reciveThrowScreenState(value) {
  sendToAPP(FuncType.searchScreenState, value.state ? "1" : "0");
}

document.addEventListener("visibilitychange", function () {
  if (!document.hidden) {
    console.log(getNowDateTime(), "页面激活");
    if (that.$route.meta && that.$route.meta.limits && that.$route.meta.limits.indexOf(LimitType.inCourse) > -1) {
      gatewaySignalR.reConnect = false;
      gatewaySignalR.startGatewaySignalR();
      // 更新分组
      if (that.$route.meta.limits.indexOf(LimitType.group) > -1) {
        that.$store.commit("SET_GROUPID", that.$store.getters.groupID);
      }
    }
  }
});
export default gatewaySignalR;



