import {
  Button,
  Cell,
  CellGroup,
  Col,
  DropdownMenu,
  DropdownItem,
  Field,
  Icon,
  Image,
  Loading,
  List,
  Picker, 
  Popup,
  RadioGroup,
  Radio,
  Row,
  Search,
  Stepper,
  Switch,
  Tab,
  Tabs,
  Tabbar,
  TabbarItem,
  Tag,
  TreeSelect, 
  Uploader
} from "vant";

export default {
  install(Vue) {
    Vue.use(Button);
    Vue.use(Cell);
    Vue.use(CellGroup);
    Vue.use(Col);
    Vue.use(DropdownMenu);
    Vue.use(DropdownItem);
    Vue.use(Field);
    Vue.use(Icon);
    Vue.use(Image);
    Vue.use(Loading);
    Vue.use(List);
    Vue.use(Picker);
    Vue.use(Popup);
    Vue.use(RadioGroup);
    Vue.use(Radio);
    Vue.use(Row);
    Vue.use(Tab);
    Vue.use(Tabs);
    Vue.use(Tabbar);
    Vue.use(TabbarItem);
    Vue.use(Tag);
    Vue.use(TreeSelect);     
    Vue.use(Search);
    Vue.use(Stepper);
    Vue.use(Switch);
    Vue.use(Uploader);
  }
};



