/**
 * 获取当前日期(格式yyyy-mm-dd)
 */
export function getNowDate() {
  let date = new Date();
  let Y = date.getFullYear() + "-";
  let M =
    (date.getMonth() + 1 < 10
      ? "0" + (date.getMonth() + 1)
      : date.getMonth() + 1) + "-";
  let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
  return Y + M + D;
}

/**
 * 获取更新日期(格式yyyy-mm-dd)
 * @param type 修改类型, date
 * @param num  增减时间量
 * @returns {string}
 */
export function getSetDate(type, num) {
  let date = new Date();
  switch (type) {
    case "date":
      date.setDate(date.getDate() + num);
      break;
    case "month":
      date.setMonth(date.getMonth() + num);
      break;
    case "year":
      date.setFullYear(date.getFullYear() + num);
      break;
    case "hour":
      date.setHours(date.getHours() + num);
      break;
    case "minute":
      date.setMinutes(date.getMinutes() + num);
      break;
  }
  let Y = date.getFullYear() + "-";
  let M =
    (date.getMonth() + 1 < 10
      ? "0" + (date.getMonth() + 1)
      : date.getMonth() + 1) + "-";
  let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
  return Y + M + D;
}

/**
 * 获取当前时间(格式yyyy-mm-dd hh:MM:ss)
 */
export function getNowDateTime() {
  let date = new Date();
  let Y = date.getFullYear() + "-";
  let M =
    (date.getMonth() + 1 < 10
      ? "0" + (date.getMonth() + 1)
      : date.getMonth() + 1) + "-";
  let D = (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";

  let h =
    (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
  let m =
    (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
    ":";
  let s = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
  return Y + M + D + h + m + s;
}

/**
 * 获取当前时间戳
 */
export function NowTimeStamp() {
  let date = new Date();
  return date.getTime();
}

/**
 * 仿guid
 */
export function newGuid() {
  var guid = "";
  for (var i = 1; i <= 32; i++) {
    var n = Math.floor(Math.random() * 16.0).toString(16);
    guid += n;
    if (i == 8 || i == 12 || i == 16 || i == 20) guid += "-";
  }
  return guid;
}

/**
 * 生成字母
 */
export function newsetDesc(num) {
  let letterArr = [];
  for (var i = 65, j = 0; j < num; i++, j++) {
    letterArr[j] = String.fromCharCode(i);
  }
  return letterArr;
}

/**
 * 简单加密
 * @param {*} str
 */
export function encrypt(str) {
  if (!str) return str;
  let timeArray = (new Date().getTime() + "sss").split("").reverse();

  let code =
    "" +
    timeArray[1] +
    timeArray[5] +
    str +
    timeArray.filter((k, q) => q > 5 && q < 9).join("");
  // 对字符串进行编码
  let encode = encodeURI(code);
  // 对编码的字符串转化base64
  return btoa(encode);//.replace("=", "");
}
/**
 * 简单解密
 * @param {*} str
 */
export function decryption(base64) {
  if (!base64) return base64;
  // 对base64转编码
  let decode = atob(base64); //+ "="

  // 编码转字符串
  let str = decodeURI(decode);
  return str.substring(2, str.length - 3) + "";
}

/**
 *  富文本图片路径转换
 * @param type 编辑类型：show展示，save存储
 * @param value
 * @param func 图片路径函数
 * @returns {*}
 */
export function transformValue(type, value, func) {
  return value.replace(
    // /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
    /<img(.*?)src="(.*?)"(.*?)>/g,
    (match, p1, p2, p3, offset, string) => {
      //capture,返回每个匹配的字符串
      if (type === "show") {
        let wordArray = p2.split("/");
        if (wordArray.length === 3 || wordArray.length === 4) {
          let filePath = "";
          filePath = func(wordArray[1], wordArray[2]);
          // if (this.routeType === 'history') {
          //   filePath = this.$mainHttp.resourse.picture(wordArray[1], wordArray[2]);
          // } else {
          //   filePath = this.$gatewayHttp.resourse.picture(wordArray[1], wordArray[2]);
          // }
          return `<img${p1}src="${filePath}"${p3}>`;
        } else {
          return `<img${p1}src="${p2}"${p3}>`;
        }
      } else {
        let index = p2.indexOf("?");
        if (index > -1) {
          let params = p2.substring(index).split("&");
          if (params.length === 2 || params.length === 3) {
            let filePath = "picture";
            params.forEach(item => {
              let sub = item.split("=");
              if (sub.length === 2) filePath += "/" + sub[1];
            });
            return `<img${p1}src="${filePath}"${p3}>`;
          }
        }
        return `<img${p1}src="${p2}"${p3}>`;
      }
    }
  );
}

/**
 * 验证是否存在表情字符
 */
// rule, value, callback
export const emojValidator = value => {
  let regRule = /\uD83C[\uDF00-\uDFFF]|\uD83D[\uDC00-\uDE4F]/g;
  return value.match(regRule);
  // if (value.match(regRule)) {
  //   return callback(new Error("不支持表情字符"));
  // } else {
  //   callback();
  // }
};

/**
 * 将base64转换为文件
 * @param {*} dataurl 
 * @param {*} filename 
 */
export const dataURLtoFile = (dataurl, filename) => {
  // eslint-disable-next-line one-var
  let arr = dataurl.split(','),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, { type: mime });
}


