import {
  getToken,
  setToken,
  removeToken,
  storages,
  setLocalStorage,
  getLocalStorage,
  setSession,
  getSession,
  removeSession
} from "@/utils/auth";
import mainHttp from "@/api/mainServer";
import gatewayHttp from "@/api/gatewayServer";
import { default as gatewaySignalR } from "@/mixin/gatewaySignalR";
import { GroupType } from "@/utils/enum";

const user = {
  state: {
    loading: false, // 页面加载状态
    requestNum: 0, // 当前请求数量
    token: getToken(),
    deviceID: getLocalStorage(storages.deviceID),
    localIP: getLocalStorage(storages.localIP),
    userId: getLocalStorage(storages.userId),
    userName: getLocalStorage(storages.userName),
    userPassword: getLocalStorage(storages.userPassword),
    serverIP: getLocalStorage(storages.serverIP),
    gatewayIP: getLocalStorage(storages.gatewayIP),
    recIP: getLocalStorage(storages.recIP),

    singleLoginReal: 0,
    singleLoginUrl: getLocalStorage(storages.singleLoginUrl),
    singleLogOutUrl: getLocalStorage(storages.singleLogOutUrl),

    student: getLocalStorage(storages.student, true),
    studentID: getLocalStorage(storages.StudentID),

    class: getLocalStorage(storages.class, true),
    classID: getLocalStorage(storages.classID),
    classStudentID: getLocalStorage(storages.classStudentID),

    course: getSession(storages.course, true),
    courseID: getSession(storages.courseID),
    nowCourseID: "",
    nowCourse: getLocalStorage(storages.nowCourse),
    group: getSession(storages.group, true),
    groupID: getSession(storages.groupID),
    groupIP: getSession(storages.groupIP),
    groupType: getSession(storages.groupType),

    screenQuestion: {},
    classPractice: {},
    groupDiscussion: {},
    boardNote: {},

    noteRefresh: false,

    // 临时课堂实录信息
    liveLession: null,
    agreeUse: getLocalStorage(storages.agreeUse)
  },
  mutations: {
    SET_TOKEN: (state, value) => {
      state.token = value;
      setToken(value);
    },
    SET_LOADING: (state, value) => {
      state.loading = value;
    },
    SET_REQUEST: (state, value) => {
      state.requestNum += value;
    },
    SET_DEVICE_ID: (state, id) => {
      state.deviceID = id;
      setLocalStorage(storages.deviceID, id);
    },
    SET_IP: (state, ip) => {
      state.localIP = ip;
      setLocalStorage(storages.localIP, ip);
    },
    SET_KEY: (state, value) => {
      state.userId = value;
      setLocalStorage(storages.userId, value, 1000 * 60 * 60 * 12);
    },
    SET_SERVERIP: (state, value) => {
      state.serverIP = value;
      setLocalStorage(storages.serverIP, value);
    },
    SET_GATEWAYIP: (state, value) => {
      state.gatewayIP = value;
      setLocalStorage(storages.gatewayIP, value);
    },
    SET_RECIP: (state, value) => {
      state.recIP = value;
      setLocalStorage(storages.recIP, value);
    },

    SET_SINGLE_LOGIN_REAL: (state, value) => {
      state.singleLoginReal = value;
      setLocalStorage(storages.singleLoginReal, value);
    },
    SET_SINGLE_LOGIN_URL: (state, value) => {
      state.singleLoginUrl = value;
      setLocalStorage(storages.singleLoginUrl, value);
    },
    SET_SINGLE_LOG_OUT_URL: (state, value) => {
      state.singleLogOutUrl = value;
      setLocalStorage(storages.singleLogOutUrl, value);
    },

    SET_USER: (state, value) => {
      state.userName = value;
      setLocalStorage(storages.userName, value);
    },
    SET_USERPASSWORD: (state, value) => {
      state.userPassword = value;
      setLocalStorage(storages.userPassword, value);
    },
    SET_STUDENT: (state, value) => {
      state.student = value;
      setLocalStorage(storages.student, value);
    },
    SET_STUDENTID: (state, value) => {
      state.studentID = value;
      setLocalStorage(storages.StudentID, value);
    },

    SET_CLASSS: (state, value) => {
      state.class = value;
      setLocalStorage(storages.class, value);
    },
    SET_CLASSSID: (state, value) => {
      state.classID = value;
      setLocalStorage(storages.classID, value);
    },
    SET_CLASSSSTUDENTID: (state, value) => {
      state.classStudentID = value;
      setLocalStorage(storages.classStudentID, value);
    },

    SET_COURSE: (state, value) => {
      state.course = value;
      setSession(storages.course, value);
    },
    SET_COURSEID: (state, value) => {
      state.courseID = value;
      setSession(storages.courseID, value);
    },
    SET_NOWCOURSEID: (state, value) => {
      state.nowCourseID = value;
    },
    SET_NOWCOURSE: (state, value) => {
      state.nowCourse = value;
      setLocalStorage(storages.nowCourse, value);
    },
    SET_GROUP: (state, value) => {
      state.group = value;
      setSession(storages.group, value, 1000 * 60 * 60 * 2);
    },
    SET_GROUPIP: (state, value) => {
      state.groupIP = value;
      setSession(storages.groupIP, value, 1000 * 60 * 60 * 2);
    },
    SET_GROUPID: (state, value) => {
      const oldGroupId = state.groupID;
      state.groupID = value;
      setSession(storages.groupID, value, 1000 * 60 * 60 * 2);
      if (oldGroupId !== value && oldGroupId) {
        window.GatewayHub.invoke("QuitGroup", oldGroupId)
          .then(res => {
            console.log("退出讨论组：", value);
          })
          .catch(err => {
            console.log("err：", err);
          });
      }

      setTimeout(() => {
        window.GatewayHub.invoke("JoinGroup", value)
          .then(res => {
            console.log("加入讨论组：", value);
            gatewaySignalR.initGroupInfo(true);
          })
          .catch(err => {
            console.log("err：", err);
          });
      }, 500);
    },
    QUIT_GROUP: state => {
      let groupID = state.groupID;
      if (groupID) {
        window.GatewayHub.invoke("QuitGroup", groupID)
          .then(res => {
            console.log("退出讨论组：", groupID);
          })
          .catch(err => {
            console.log("err：", err);
          });
      }
      state.group = "";
      state.groupIP = null;
      state.groupID = null;
      setSession(storages.groupID, "", 1000 * 60 * 60 * 2);
      setSession(storages.groupIP, null, 1000 * 60 * 60 * 2);
      setSession(storages.group, null, 1000 * 60 * 60 * 2);
      gatewaySignalR.initGroupInfo(false); // 更新分组信息给APP
    },

    SET_GROUPTYPE: (state, value) => {
      state.groupType = value;
      setSession(storages.groupType, value);
    },

    SET_SCREENQUESTION: (state, value) => {
      state.screenQuestion = value;
    },
    SET_CLASSPRACTICE: (state, value) => {
      state.classPractice = value;
    },
    SET_GROUPDISCUSSION: (state, value) => {
      state.groupDiscussion = value;
    },
    SET_BOARDNOTE: (state, value) => {
      state.boardNote = value;
    },

    SET_NOTEREFRESH: (state, value) => {
      state.noteRefresh = value;
    },

    SET_LIVELESSION: (state, value) => {
      state.liveLession = value;
    },
    SET_AGREEUSE: (state, value) => {
      state.agreeUse = value;
      setLocalStorage(storages.agreeUse, value);
    },

    /**
     * 退出课程了
     * @param {*} state
     * @param {*} value
     */
    QUIT_COURSE(state) {
      state.course = null;
      removeSession(storages.course);
      state.courseID = "";
      removeSession(storages.courseID);
      state.group = null;
      removeSession(storages.group);
      state.groupID = null;
      removeSession(storages.groupID);
      state.groupType = null;
      removeSession(storages.groupType);

      state.screenQuestion = {};
      state.classPractice = {};
      state.groupDiscussion = {};
    }
  },

  actions: {
    /**
     * 登录请求
     * @param {*} datas {userInfo, other}
     * @returns {Promise<any>}
     */
    LoginByUser({ commit, state }, datas) {
      return new Promise((resolve, reject) => {
        mainHttp.account
          .postLogin(datas.userInfo, datas.other)
          .then(res => {
            if (res.Result) {
              if (state.userName !== res.UserInfo.UserName) {
                commit("SET_NOWCOURSE", null);
                commit("SET_NOWCOURSEID", null);
              }
              commit("SET_TOKEN", res.Data);
              commit("SET_KEY", res.UserInfo.UserID);
              commit("SET_USER", res.UserInfo.UserName);
              commit("SET_STUDENT", res.Student);
              commit("SET_STUDENTID", res.Student ? res.Student["StudentID"] : "");
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 加入班级请求
     * @param {*} reqData
     * @returns {Promise<any>}
     */
    JoinClassByUser({ commit }, reqData) {
      return new Promise((resolve, reject) => {
        mainHttp.class.joinClass(reqData).then(res => {
          if (res.Result) {
            commit("SET_CLASSSID", res["ClassID"]);
            commit("SET_CLASSSSTUDENTID", res["CSID"]);
            commit("SET_COURSEID", res["CIID"]);
          }
          resolve(res);
        }).catch(error => {
          reject(error);
        });
      });
    },

    /**
    * 课程校验
    * @param {*} userInfo
    * @returns {Promise<any>}
    */
    ValidateCourse({ commit }, reqData) {
      return new Promise((resolve, reject) => {
        gatewayHttp.student.validateCourse()
          .then(res => {
            if (res.Result && res.Data) {
              commit("SET_NOWCOURSEID", res.Data.CIID);
              if (res.Data.CIID !== user.state.courseID) {
                commit("SET_NOWCOURSE", null);
                resolve(true);
              }
            }
            resolve(false);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 获取课程服务请求
     * @param {*} classRoomId
     * @returns {Promise<any>}
     */
    GetCourseServiceByUser({ commit }, classRoomId) {
      return new Promise((resolve, reject) => {
        mainHttp.interactConfig
          .getData({ clsRoomID: classRoomId })
          .then(res => {
            if (res.Result) {
              commit("SET_GATEWAYIP", res.Data["InIP"]);
              commit("SET_RECIP", JSON.parse(res.Data["ConfigInfo"]).RecIP);
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 获取当前分组
     * @returns {Promise<any>}
     */
    GetGroupByUser({ commit }) {
      return new Promise((resolve, reject) => {
        gatewayHttp.group
          .getList({
            studentID: user.state.studentID
          })
          .then(res => {
            if (res.Result && res.Data) {
              let groupItem = {
                gatewayID: res.Data["GatewayID"],
                groupID: res.Data["GroupID"],
                groupName: res.Data["GroupName"],
                groupMax: res.Data["GroupNumber"],
                groupingID: res.Data["GroupingID"],
                studentID: res.Data["StudentID"],
                memberList: res["memberList"],
                currentNum: res["memberList"] ? res["memberList"].length : 0
              };

              let groupIP = res["GatewayIP"] || "";
              commit("SET_GROUPIP", groupIP);
              commit("SET_GROUP", groupItem);
              // if (!user.state.groupID || user.state.groupID !== res.Data["GroupID"]) {
              commit("SET_GROUPID", res.Data["GroupID"]);
              // }
              if (res.GroupingType) commit("SET_GROUPTYPE", res.GroupingType);
            } else {
              commit("QUIT_GROUP");
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 加入分组
     * @param {*} groupID
     * @param {*} group
     */
    JoinGroup({ commit }, groupInfo) {
      return new Promise((resolve, reject) => {
        gatewayHttp.group
          .postJoinGroup({
            studentID: user.state.studentID,
            groupID: groupInfo.groupID
          })
          .then(res => {
            if (res.Result) {
              commit("SET_GROUPID", groupInfo.groupID);
              commit("SET_GROUP", groupInfo.group);
              commit("SET_GROUPIP", groupInfo.group["gatewayIP"] || "");
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 退出当前分组
     */
    QuitGroup({ commit }) {
      return new Promise((resolve, reject) => {
        gatewayHttp.group
          .postQuitGroup({
            groupID: user.state.groupID,
            studentID: user.state.studentID
          })
          .then(res => {
            if (res.Result) {
              commit("QUIT_GROUP");
              commit("SET_GROUPTYPE", GroupType.Free);
            }
            resolve(res);
          })
          .catch(error => {
            console.log(error);
            reject(error);
          });
      });
    },

    // user logout
    logout({ commit, state }) {
      return new Promise((resolve, reject) => {
        commit("SET_KEY", "");
        commit("SET_STUDENT", null);
        commit("SET_STUDENTID", "");
        commit("SET_TOKEN", null);
        removeToken();
        resolve();
      });
    }
  }
};

export default user;



