const courseSpaceRouter = [
    {
        path: "/space/homework",
        name: "spaceHomework",
        meta: { title: "任务列表", from: "/home-page", cache: true },
        component: () => import("@/pages/space/task/homework.vue")
    },
    {
        path: "/space/practiceDone/:taskId/:libraryId/:practiceId/:classHourId",
        name: "spacePracticeDone",
        meta: { title: "随堂练习", from: "", cache: true },
        component: () => import("@/pages/space/task/practiceDone.vue")
    },
    {
        path: "/space/practiceView/:taskId/:libraryId/:practiceId/:classHourId",
        name: "spacePracticeView",
        meta: { title: "随堂练习", from: "/space/homework", cache: true },
        component: () => import("@/pages/space/task/practiceView.vue")
    },
    {
        path: "/space/coursewareComment/:coursewareId/:courseId",
        name: "spaceCoursewareComment",
        meta: { title: "课件点评", from: "/space/homework", cache: true },
        component: () => import("@/pages/space/task/coursewareComment.vue")
    },

    {
        path: "/space/note",
        name: "spaceNote",
        meta: { title: "我的笔记", from: "/home-page", cache: true },
        component: () => import("@/pages/space/note/note.vue")
    },
    {
        path: "/space/noteView",
        name: "spaceNoteView",
        meta: { title: "笔记信息", from: "", cache: true },
        component: () => import("@/pages/space/note/noteView.vue")
    },
    {
        path: "/space/noteEdit",
        name: "spaceNoteEdit",
        meta: { title: "笔记编辑", from: "", cache: true },
        component: () => import("@/pages/space/note/noteEdit.vue")
    }
];

export default courseSpaceRouter;


