  singleLoginUrl: state => state.user.singleLoginUrl,
  singleLogOutUrl: state => state.user.singleLogOutUrl,

  student: state => state.user.student,
  studentID: state => state.user.studentID,

  class: state => state.user.class,
  classID: state => state.user.classID,
  classStudentID: state => state.user.classStudentID,

  course: state => state.user.course,
