/**
 * 和APP壳交互的方法名称
 */
export const FuncType = {
  // 强制退出APP
  exitAPP: "SystemExit",
  // 退出APP
  closeAPP: "openBackDialog",
  // 开启摄像机
  cameraOpen: "OpenCamera",
  // 开启相册
  albumOpen: "openPick",
  // 开启扫码
  openQRcode: "OpenQRcode",
  // 跳转链接
  toPath: "sendToPath",
  // 获取IP信息
  initLocalIP: "GetIPAdress",
  // 发送查询的网关IP
  searchIP: "sendPrjScreenIP",
  // 发送查询的投屏状态
  searchScreenState: "sendPrjScreenState",
  // 发送已登录
  hadLogin: "login",
  // 发送开始广播
  broadcastStart: "sendStartBroadcast",
  // 发送开始广播地址
  broadcastStartUrl: "sendStartBroadcastByUrl",
  // 发送停止广播
  broadcastStop: "sendStopBroadcast",
  // 发送打开投屏
  throwScreenOpen: "openPrjScreen",
  // 发送开始投屏
  throwScreenStart: "openMultiPointPrj",
  // 发送停止投屏
  throwScreenStop: "closeMultiPointPrj",
  // 发送开始被动投屏
  beThrowScreenStart: "openMultiBePointPrj",
  // 发送停止被动投屏
  beThrowScreenStop: "closeMultiBePointPrj",
  // 发送教师屏幕配置信息
  screenSetting: "sendSystemInfo",
  // 发送学生分组信息
  groupInfo: "sendGroupMsg",
  // 发送下载命令
  downloadFile: "downloadFile"
};

/**
 * 主动发送信息给APP壳
 * @param {*} funcName
 * @param {*} value
 * @param {*} count 发送次数,默认1次
 * @param {*} time 发送间隔时间
 */
export function sendToAPP(funcName, value = "", count = 1, time = 2000) {
  return new Promise((resolve, reject) => {
    if (!window.Android && !window.webkit) {
      resolve("");
      return;
    }
    // console.log("开始发送信息给APP:" + funcName + ",值：" + value);
    sendFunction(funcName, value, count, time, 0).then(res => {
      resolve(res);
    });
  });
}

function sendFunction(funcName, value, count, time, index) {
  return new Promise((resolve, reject) => {
    index++;
    let state = false;
    try {
      if (window.Android) {
        if (value !== "") {
          state = window.Android[funcName](value);
        } else {
          state = window.Android[funcName]();
        }
        console.log("发给安卓APP:" + funcName + ",值：" + value + ",返回值：" + state);
      } else if (window.webkit) {
        // window.webkit.messageHandlers.方法名.postMessage(参数);
        if (value !== "") {
          state = window.webkit.messageHandlers[funcName].postMessage(value);
        } else {
          state = window.webkit.messageHandlers[funcName].postMessage(null);
        }
        console.log("发给苹果APP:" + funcName + ",值：" + value + ",返回值：" + state);
      }
    } catch (err) {
      console.log("发壳方法," + funcName + ",异常：" + JSON.stringify(err));
      console.log("err:", err);
    }

    if (state || index >= count) {
      resolve(state || "");
    } else {
      setTimeout(() => {
        sendFunction(funcName, value, count, time, index);
      }, time);
    }
  });
}



