/**
 * 随堂练习状态
 */
export const ClassPracticeState = {
  noStart: 1,
  start: 2, //答题开始
  end: 3, //答题结束，可提交答案
  forbid: 4 //禁止答题
};
/**
 * 随堂练习状态 名称
 */
export function ClassPracticeStateName(val) {
  if (val === 1) {
    return "未开始";
  } else if (val === 2) {
    return "进行中";
  } else if (val === 3) {
    return "结束答题";
  } else if (val === 4) {
    return "停止答题";
  } else {
    return "未知";
  }
}

