import { getNowDateTime } from "@/utils/index";
import { ModulePicture, LimitType } from "@/utils/enum";
export default {
  data() {
    return {};
  },
  methods: {
    isAutoNote() {
      window.uploadImage = imgFile => {
        this.receiveNote(imgFile);
      };
    },
    // notAutoNote() {
    //   window.uploadImage = imgFile => {
    //     this.afterRead(imgFile);
    //   };
    // },
    receiveNote(file) {
      // 防止其它页面做笔记，导致异常
      if (
        this.$route &&
        this.$route.meta &&
        this.$route.meta.limits &&
        this.$route.meta.limits.indexOf(LimitType.inCourse) > -1
      ) {
        if (!this.$store.getters.courseID || !this.$store.getters.studentID) {
          return;
        }
        console.log("开始保存笔记:" + file);

        if (file) {
          let reqData = {
            funType: ModulePicture.StudentNote,
            imgBaseString: file
          };
          this.$gatewayHttp.resourse
            .upload(reqData)
            .then(res => {
              if (res.Result) {
                this.saveNote(res.Data);
              }
            })
            .catch(err => {
              this.$toast("添加笔记失败");
            });
        }
      }
    },
    saveNote(imgId) {
      this.$gatewayHttp.note
        .postSaveEdit({
          CIID: this.$store.getters.courseID,
          studentID: this.$store.getters.studentID,
          snTitle: getNowDateTime(),
          snContent: `<img src="picture/${imgId}/${ModulePicture.StudentNote}" alt="" />`
        })
        .then(res => {
          if (res.Result) {
            this.$toast.success("新增笔记成功");
            if (this.$route.path.indexOf("/note") === -1) {
              this.$router.push("/note");
            } else {
              this.$store.commit("SET_NOTEREFRESH", true);
            }
          }
        })
        .catch(err => {
          this.$toast("添加笔记失败");
        });
    }
  },
  watch: {
    $route(newVal, oldVal) {
      if (newVal && newVal.meta && newVal.meta.unAutoNote) {
        // this.notAutoNote();
      } else if (newVal && newVal.meta && newVal.meta.limits && newVal.meta.limits.indexOf(LimitType.inCourse) > -1) {
        this.$nextTick(() => {
          this.isAutoNote(newVal);
        });
      }
    }
  }
};



