import Vue from "vue";
import App from "./App";
import router from "./router";
// import Vant from "vant";
import "signalr";
import "./permission";
import "./utils/errLog"; // error log
import store from "./store";

import "vant/lib/index.css";

// import "./assets/style/base.css";
import "./assets/font/iconfont.css";
import 'amfe-flexible/index.js'

import VantComponents from "./utils/components.js";

import { Toast, PullRefresh, Lazyload } from "vant";
import RLackData from "./components/r-lack-data";
import HeadTop from "./components/HeadTop";
import { formatDate } from "@/utils/formatData";
import { default as gatewaySignalR } from "@/mixin/gatewaySignalR";
import mainHttp from "@/api/mainServer";
import gatewayHttp from "@/api/gatewayServer";
import promise from "es6-promise";
promise.polyfill();
// import vant from "@/vant";
Vue.prototype.$mainHttp = mainHttp;
Vue.prototype.$gatewayHttp = gatewayHttp;
Vue.prototype.$config = appConfig;

// const log = console.log;
// console.log = (...args) => {
//   // args = args.map(arg => `输入: ${arg}`);
//   if (appConfig.showLog) log.apply(console, args);
// };

Vue.use(Toast);
Vue.use(PullRefresh);
Vue.use(VantComponents);
Vue.use(Lazyload, {
  lazyComponent: true
});
Vue.component(RLackData.name, RLackData);
Vue.component(HeadTop.name, HeadTop);

// Vue.use(vant);
// Vue.use(Vant);

Vue.config.productionTip = false;
Vue.prototype.$gatewaySignalR = gatewaySignalR;
Vue.filter("formatDay", time => {
  if (time) {
    return formatDate(time, "yyyy-MM-dd");
  } else {
    return "-";
  }
});
Vue.filter("formatDate", time => {
  if (time) {
    return formatDate(time, "yyyy-MM-dd hh:mm");
  } else {
    return "-";
  }
});
Vue.filter("formatTime", time => {
  if (time && time.length > 4) {
    return time.substring(0, 5);
  } else {
    return "-";
  }
});
Vue.filter("formatMinutesTime", time => {
  if (time) {
    return formatDate(time, "hh:mm");
  } else {
    return "-";
  }
});

new Vue({
  el: "#app",
  router,
  store,
  components: { App },
  template: "<App/>",
  created() {
    gatewaySignalR.init(this);
  }
});



