import { LimitType } from "@/utils/enum";
const courseRouter = [
    {
        path: "/course",
        name: "course",
        meta: {
            title: "课程",
            from: "/home-course",
            cache: true,
            limits: [LimitType.inCourse]
        },
        component: () => import("@/pages/course/course.vue")
    },
    {
        path: "/signIn/:way/:state?",
        name: "signIn",
        meta: { title: "签到", from: "/course", limits: [LimitType.inCourse] },
        component: () => import("@/pages/course/sign/signIn.vue")
    },
    {
        path: "/blackboardWriting",
        name: "boardNote",
        meta: { title: "板书", from: "/course", limits: [LimitType.inCourse] },
        component: () => import("@/pages/course/boardNote/blackboardWriting.vue")
    },
    {
        path: "/groupNote",
        name: "groupNote",
        meta: { title: "小组笔记", from: "/course", limits: [LimitType.inCourse] },
        component: () => import("@/pages/course/groupNote/index.vue")
    },
    {
        path: "/quiz/:selected?",
        name: "quiz",
        meta: {
            title: "随堂测验",
            from: "/course",
            cache: true,
            limits: [LimitType.inCourse]
        },
        component: () => import("@/pages/course/quiz/index.vue")
    },
    {
        path: "/screenQuestion/:id?",
        name: "screenQuestion",
        meta: {
            title: "截屏提问",
            from: "/quiz",
            limits: [LimitType.inCourse],
            unAutoNote: true
        },
        component: () => import("@/pages/course/quiz/screenQuestion.vue")
    },
    {
        path: "/classPractice/:id?/:bankId?/:total?/:state?",
        name: "classPractice",
        meta: {
            title: "随堂练习",
            from: "/quiz",
            limits: [LimitType.inCourse],
            unAutoNote: true
        },
        component: () => import("@/pages/course/quiz/classPractice.vue")
    },
    {
        path: "/note",
        name: "note",
        meta: {
            title: "笔记",
            from: "/course",
            cache: true,
            limits: [LimitType.inCourse]
        },
        component: () => import("@/pages/course/note/note.vue")
    },
    {
        path: "/noteDetail",
        name: "noteDetail",
        meta: { title: "笔记详情", from: "/note", limits: [LimitType.inCourse] },
        component: () => import("@/pages/course/note/noteDetail.vue")
    },
    {
        path: "/editNote",
        name: "editNote",
        meta: {
            title: "编辑笔记",
            from: "/note",
            limits: [LimitType.inCourse],
            unAutoNote: true
        },
        component: () => import("@/pages/course/note/editNote.vue")
    },
    {
        path: "/question",
        name: "question",
        meta: { title: "提问", from: "/course", limits: [LimitType.inCourse] },
        component: () => import("@/pages/course/question/question.vue")
    },
    {
        path: "/courseware",
        name: "courseware",
        meta: { title: "课件", from: "", limits: [LimitType.inCourse, LimitType.afterCourse] },
        component: () => import("@/pages/course/courseware/index.vue")
    },
    {
        path: "/fileView/:id/:fileExt/:name",
        name: "fileView",
        meta: { title: "课件预览", from: "" },
        component: () => import("@/pages/course/courseware/fileView.vue")
    },
    {
        path: "/coursewareView/:id/:fileExt/:name",
        name: "coursewareView",
        meta: {
            title: "课件预览",
            from: "/courseware",
            limits: [LimitType.inCourse, LimitType.afterCourse]
        },
        component: () => import("@/pages/course/courseware/fileView.vue")
    },
    {
        path: "/liveLesson",
        name: "liveLesson",
        meta: {
            title: "课堂实况列表",
            from: "/course",
            limits: [LimitType.inCourse]
        },
        component: () => import("@/pages/course/liveLesson/index.vue")
    },
    {
        path: "/liveLessonType",
        name: "liveLessonType",
        meta: {
            title: "课堂实况类型",
            from: "",
            limits: [LimitType.inCourse, LimitType.afterCourse]
        },
        component: () => import("@/pages/course/liveLesson/typeList.vue")
    },
    {
        path: "/livePlayback/:type/:index",
        name: "livePlayback/:type/:index",
        meta: {
            title: "课堂实况回放",
            from: "",
            limits: [LimitType.inCourse, LimitType.afterCourse]
        },
        component: () => import("@/pages/course/liveLesson/playback.vue")
    },

    {
        path: "/groupDiscussion",
        name: "groupDiscussion",
        meta: {
            title: "小组讨论",
            from: "/course",
            limits: [LimitType.inCourse, LimitType.group],
            unAutoNote: true
        },
        component: () => import("@/pages/course/group/groupDiscussion.vue")
    },
    {
        path: "/teamRank",
        name: "teamRank",
        meta: {
            title: "小组风云",
            from: "",
            limits: [LimitType.inCourse, LimitType.group]
        },
        component: () => import("@/pages/course/group/teamRank.vue")
    },
    {
        path: "/joinGroup",
        name: "joinGroup",
        meta: {
            title: "加入小组",
            from: "/course",
            limits: [LimitType.inCourse]
        },
        component: () => import("@/pages/course/group/joinGroup.vue")
    },
    {
        path: "/groupInfo/:groupId?",
        name: "groupInfo",
        meta: {
            title: "小组信息",
            from: "/joinGroup",
            limits: [LimitType.inCourse]
        },
        component: () => import("@/pages/course/group/groupInfo.vue")
    }
];

export default courseRouter;


