<template>
  <div>
    <div id="headTop">
      <slot class="left" name="left"></slot>
      <span class="header_title" @click="titleClick">{{title}}</span>
      <!-- <span
        class="log"
        @click="clickCount > 1 ? (showLog = true) : clickCount++"
      >&nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp;</span> -->
      <slot></slot>
      <span v-if="showBack" @click="goBack" class="head-back">
        <i class="iconfont icon-fanhui"></i>
      </span>

      <slot class="a" name="right"></slot>
    </div>
    <div class="head_seat"></div>

  </div>
</template>

<script>
export default {
  name: "head-top",
  props: {
    title: String,
    showBack: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      clickCount: 0,
      showLog: false
    };
  },
  computed: {},
  watch: {},
  created() {
    if (this.title === "我的首页" && this.title === "首页课程") {
      if ($(".head-back").length > 0) {
        $(function() {
          $(".head-back").css("display", "none");
        });
      }
    }
  },
  methods: {
    goBack() {
      // 指定返回页，则跳转指定页
      if (this.$route.meta && this.$route.meta.from) {
        this.$router.replace(this.$route.meta.from);
      } else {
        this.$router.go(-1);
      }
    },
    titleClick() {
      this.$emit("touchClick", null);
      // this.clickCount > 8 ? (this.showLog = true) : this.clickCount++
    }
  }
};
</script>

<style lang='less'>
@import url("./../styles/common/base.less");
@height: 44px;
#headTop {
  width: 100%;
  height: @height;
  background: rgba(0, 203, 171, 1);
  position: fixed;
  top: 0;
  z-index: 999;
  .left {
    width: 20px;
    padding: 20px;
    border: 1px solid #000;
  }

  .head-back {
    margin-left: 4vw;
    line-height: @height;
    font-size: @font-title;
    color: #fff;
    position: absolute;
    left: 0;

    .iconfont {
      font-size: @font-title;
    }
  }

  .header_title {
    text-align: center;
    color: #fff;
    font-size: @font-sub-title;
    line-height: @height;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .head-right {
    float: right;
    margin-right: 16px;
    font-size: @font-title * 1.1;
    line-height: @height;
    color: #fff;

    .head_span {
      font-size: @font-info;
    }
    .van-icon {
      font-weight: 800;
    }
  }
}
.head_seat {
  width: 100%;
  height: @height;
  background: rgba(0, 203, 171, 1);
}
</style>


