<template>
  <div id="index">
    <keep-alive :max="5">
      <router-view></router-view>
    </keep-alive>
    <foot-bar></foot-bar>
  </div>
</template>

<script>
import FootBar from "@/components/FootBar";

export default {
  name: "",
  components: { FootBar },
  props: {},
  data() {
    return {};
  }
};
</script>

<style lang='less'>
#index {
  width: 100%;
  min-height: 100%;
  background: #ebf0ef;
  position:relative;
}
</style>


