import Vue from "vue";
import Router from "vue-router";
import courseRouter from "./modules/course";
import courseHistoryRouter from "./modules/history";
import courseSpaceRouter from "./modules/space";
import { LimitType } from "@/utils/enum";

import IndexPage from "@/pages/home/index";

Vue.use(Router);

const routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/login",
    meta: { title: "登录", from: "", limits: [LimitType.top] },
    component: () => import("@/pages/account/login.vue")
  },
  {
    path: "/agreement",
    meta: { title: "服务协议", from: "" },
    component: () => import("@/pages/user/agreement.vue")
  },
  {
    path: "/policy",
    meta: { title: "隐私政策", from: "" },
    component: () => import("@/pages/user/policy.vue")
  },
  {
    path: "/index",
    name: "index",
    component: IndexPage,
    redirect: "/home-page",
    children: [
      {
        path: "/home-page",
        name: "homePage",
        meta: { title: "首页", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/home/home-page.vue")
      },
      {
        path: "/home-course",
        name: "homeCourse",
        meta: { title: "课程", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/home/home-course.vue")
      },
      {
        path: "/my-zone",
        name: "myZone",
        meta: {
          title: "个人中心",
          from: "",
          cache: true,
          limits: [LimitType.top]
        },
        component: () => import("@/pages/home/my-zone.vue")
      }
    ]
  },
  //我的个人信息
  {
    path: "/personal",
    component: IndexPage,
    redirect: "/personal-data",
    children: [
      {
        path: "/personal-data",
        meta: { title: "个人资料", from: "/my-zone" },
        component: () => import("@/pages/user/personal-data.vue")
      },
      {
        path: "/help-use",
        meta: { title: "使用说明", from: "/my-zone" },
        component: () => import("@/pages/user/help-use.vue")
      },
      {
        path: "/my-stats",
        meta: { title: "我的统计", from: "/my-zone" },
        component: () => import("@/pages/user/my-stats.vue")
      },
      {
        path: "/about-us",
        meta: { title: "关于我们", cache: true, from: "/my-zone" },
        component: () => import("@/pages/user/about-us.vue")
      }
    ]
  },
  { path: "*", redirect: "/" } //异常页面
];

export default new Router({
  routes: routes
    .concat(courseRouter)
    .concat(courseHistoryRouter)
    .concat(courseSpaceRouter)
});



