import axios from "axios";
import store from "@/store";
import { Toast } from "vant";
// import config from "./../../static/config.json";
const setting = appConfig;

const service = axios.create();
service.defaults.timeout = 1000 * 30; // 请求超时 时长

service.interceptors.request.use(
  config => {
    // do something before request is sent
    config.baseURL = "http://" + store.getters.gatewayIP + ":" + setting.gatewaySeverPort;
    // if (store.getters.userId) config.params = { ...config.params, token: store.getters.userId };
    store.commit("SET_REQUEST", 1);
    return config;
  },
  error => {
    console.log("request=>error:", error);
    if (error.code == "ECONNABORTED" && error.message.indexOf("timeout") > -1) {
      Toast("请求超时~");
    }
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data;
    store.commit("SET_REQUEST", -1);
    if (!res.Result && res.Info) {
      Toast(res.Info);
    }
    return res;
  },
  error => {
    console.log("response=>error:", error); // for debug
    if (error.code == "ECONNABORTED" && error.message.indexOf("timeout") > -1) {
      Toast("请求超时~");
    } else if (error.message.indexOf("Network Error") > -1) {
      Toast("请求失败");
    }
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

const basePath = "/Interact";

const BoardNote = `${basePath}/BoardNoteService/`;
const Class = `${basePath}/ClassService/`;
const ClassPractise = `${basePath}/ClassPractiseService/`;
const Gateway = `${basePath}/GatewayService/`;
const Group = `${basePath}/GroupService/`;
const GroupDiscussion = `${basePath}/GroupWallService/`;
const GroupNote = `${basePath}/GroupNoteService/`;
const Note = `${basePath}/StudentNoteService/`;
const Question = `${basePath}/QuestionService/`;
const ScreenQustion = `${basePath}/ScreenSubjectService/`;
const Sign = `${basePath}/SignService/`;
const Student = `${basePath}/StudentService/`;
export default {
  get: (httpUrl, data) => {
    return service({
      url: httpUrl,
      method: "get",
      params: data
    });
  },
  post: (httpUrl, data) => {
    return service({
      url: httpUrl,
      method: "post",
      data
    });
  },
  resourse: {
    picture: (id, type) => {
      return `http://${store.getters.gatewayIP}:${setting.gatewaySeverPort}/GetPicture?fileName=${id}&fileModule=${type}`;
    },
    video: (id, fileType) => {
      return `http://${store.getters.recIP}:${setting.gatewaySeverPort}/${id}.${fileType}`;
    },
    upload: data => {
      return service({
        url: `/UploadImageByBaseString`,
        method: "post",
        data
      });
    }
  },
  boardNote: {
    getList: data => {
      return service({
        url: `${BoardNote}GetDatas`,
        method: "get",
        params: data
      });
    }
  },
  class: {
    joinClass: data => {
      return service({
        url: `${Class}JoinClass`,
        method: "get",
        params: data
      });
    }
  },
  classPractise: {
    // 获取练习列表
    getList: data => {
      return service({
        url: `${ClassPractise}GetDatas`,
        method: "get",
        params: data
      });
    },
    // 根据题目编号获取题目
    getData: data => {
      return service({
        url: `${ClassPractise}GetPSData`,
        method: "get",
        params: data
      });
    },
    // 根据练习编号和序号获取题目
    getDataByIndex: data => {
      return service({
        url: `${ClassPractise}GetPSDataInfo`,
        method: "get",
        params: data
      });
    },
    // 提交答案
    postDataAnswer: data => {
      return service({
        url: `${ClassPractise}SubmitAnswer`,
        method: "post",
        data
      });
    },
    // 提交练习
    postDataPractise: data => {
      return service({
        url: `${ClassPractise}SubmitPractise`,
        method: "post",
        data
      });
    },
    // 提交问卷调查
    postDataSurvey: data => {
      return service({
        url: `${ClassPractise}SubmitSurvey`,
        method: "post",
        data
      });
    }
  },
  gateway: {
    getScreenState: data => {
      return service({
        url: `${Gateway}GetForScreen`,
        method: "get",
        params: data
      });
    },
    getScreenInfo: data => {
      return service({
        url: `${Gateway}CheckScreenCode`,
        method: "get",
        params: data
      });
    },
    linkTest: data => {
      return axios({
        url: `http://${store.getters.gatewayIP}:${setting.gatewaySeverPort}${Gateway}LinkTest`,
        method: "get",
        timeout: 1 * 1000,
        params: data
      });
    }
  },
  group: {
    // 组信息或组成员
    getList: data => {
      return service({
        url: `${Group}GetStudentGroup`,
        method: "get",
        params: data
      });
    },
    // 加入组
    postJoinGroup: data => {
      return service({
        url: `${Group}JoinGroup`,
        method: "post",
        data
      });
    },
    // 修改组名
    postDataSave: data => {
      return service({
        url: `${Group}UpdateGroupName`,
        method: "post",
        data
      });
    },
    // 退出组
    postQuitGroup: data => {
      return service({
        url: `${Group}QuitGroup`,
        method: "post",
        data
      });
    },
    // 组排名
    getRankList: data => {
      return service({
        url: `${Group}GetGroupMemberRanking`,
        method: "get",
        params: data
      });
    }
  },
  groupDiscussion: {
    // 论题及历史数据
    getList: data => {
      return service({
        url: `${GroupDiscussion}GetGSAndGDDatas`,
        method: "get",
        params: data
      });
    },
    // 论题统计
    getStatistic: data => {
      return service({
        url: `${GroupDiscussion}GetGSStatsByGroupID`,
        method: "get",
        params: data
      });
    },
    // 提交讨论
    postDataSave: data => {
      return service({
        url: `${GroupDiscussion}SubmitGroupSubject`,
        method: "post",
        data
      });
    }
  },
  groupNote: {
    // 获取数据
    getList: data => {
      return service({
        url: `${GroupNote}GetDatas`,
        method: "get",
        params: data
      });
    }
  },
  note: {
    getList: data => {
      return service({
        url: `${Note}GetDatas`,
        method: "get",
        params: data
      });
    },
    postSaveEdit: data => {
      return service({
        url: `${Note}Add`,
        method: "post",
        data
      });
    },
    postDelete: data => {
      return service({
        url: `${Note}Delete`,
        method: "post",
        data
      });
    }
  },
  screenQustion: {
    // 截屏提问数据
    getList: data => {
      return service({
        url: `${ScreenQustion}GetDatas`,
        method: "get",
        params: data
      });
    },
    // 截屏提问明细
    getData: data => {
      return service({
        url: `${ScreenQustion}GetData`,
        method: "get",
        params: data
      });
    },
    // 提交答案
    postDataAnswer: data => {
      return service({
        url: `${ScreenQustion}SubmitAnswer`,
        method: "post",
        data
      });
    },
    // 提交问卷调查
    postDataSurvey: data => {
      return service({
        url: `${ScreenQustion}SubmitSurvey`,
        method: "post",
        data
      });
    },
    // 提交抢答
    postDataResponder: data => {
      return service({
        url: `${ScreenQustion}StartResponder`,
        method: "post",
        data
      });
    }
  },
  sign: {
    dataList: data => {
      return service({
        url: `${Sign}GetDatas`,
        method: "get",
        params: data
      });
    },
    getSign: data => {
      return service({
        url: `${Sign}SignIn`,
        method: "get",
        params: data
      });
    }
  },
  student: {
    getRegister: data => {
      return service({
        url: `${Student}Register`,
        method: "get",
        params: data
      });
    },
    getScreenInfo: data => {
      return service({
        url: `${Student}GetThrowScreenInfo`,
        method: "get",
        params: data
      });
    },
    validateCourse: data => {
      return service({
        url: `${Student}GetClassInfo`,
        method: "get",
        params: data
      });
    },
    setThrowScreenState: data => {
      return service({
        url: `${Student}GetThrowScreenState`,
        method: "get",
        params: data
      });
    }
  },
  question: {
    getList: data => {
      return service({
        url: `${Question}GetDatas`,
        method: "get",
        params: data
      });
    },
    postDataSave: data => {
      return service({
        url: `${Question}Add`,
        method: "post",
        data
      });
    }
  },

};



