 *  */
export default {
  name: "r-lack-data",
  props: {
    msg: {
      type: String,
      default: "暂无数据"
    }
  },
  data() {
    return {};
  }
};
/****
 * 缺少数据显示用语
 *
 *  */
</script>
<style lang="less"  scoped>
.line_notice {
  text-align: center;
  padding: 10px;
  color: #2c3e50;
  font-size: 14px;
}
</style>


